import server from '../../server.js';
import md5 from '../../static/md5.js';
export default {
	data() {
		return {
			xzId:1,
			showVip:false,
			gaodu:'-710px',
			yanse:'rgba(0,0,0,0)',
			userInfo:[],
			monnumber:100,
			money:0,
			endVipTime:'0',
			ann:false,
			xz:1,
			angelibi:"自定义",
			billList:[],
			apptype:'xcx'
		}
	},
	onShow:function(){
		this.userInfo=server.userinfo;
		this.getjifen();
	},
	onLoad:function(){
		this.userInfo=server.userinfo;
		this.getjifen();
	},
	methods: {
		get:function(e){
			if(e==1){
				//微信
				uni.navigateTo({
					url: 'tixian?type=wx'
				});
			}else{
				//支付宝
				uni.navigateTo({
					url: 'tixian?type=alipay'
				});
			}
		},
		getjifen:function(){
			console.log("获取积分cookie：",server.cookie)
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=cxjf", //仅为示例，并非真实接口地址。
				data: {
					auid:server.userinfo.Auid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.endVipTime=res.data.data.Points
					}else{
						
					}
					console.log(this.msgNumber)
				},
				complete() {
					
				}
			});
		},
		getVip:function(){
			var shebei=uni.getSystemInfoSync().platform;
			if(shebei=='ios'){
				uni.showToast({
					title: "ios系统暂不提供充值，详情请咨询客服",
					position:'bottom',
					icon:'none'
				});
				return;
			}
				
			
			if(this.monnumber>=10){
				
				this.money=this.monnumber/10
				if(this.money<1){
					uni.showToast({
						title: "最低充值不低于1元",
						position:'bottom',
						icon:'none'
					});
					return;
				}
				console.log('安个利币：'+this.monnumber,'金额：'+this.money)
				uni.showLoading({
					title: '加载中'
				});
				let wxkey=md5('不停'+String(Date.now()));
				//如果是APP支付





































































































				

				uni.request({
					method:'POST',
					url: "https://api.angeli.top/WeChat/pay.php?type=angelibi", //仅为示例，并非真实接口地址。
					data: {
						openid:server.userinfo.wxOpenId,
						fee:this.money,
						number:this.monnumber,
						key:wxkey,
						auid:server.userinfo.Auid,
						payType:this.apptype,
						token:server.token
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						
					},
					success: (res) => {
						console.log(res)
						let dd=res.data.orderId;
						if(res.data.code=="1"){
							let sjstr=md5(String(Date.now()));
							let time=String(Date.now())
							let signTemp="appId=wxb2418420ae2cf37c&nonceStr="+sjstr+"&package=prepay_id="+res.data.data.prepay_id+"&signType=MD5&timeStamp="+time+"&key=xinfenghuliankejiyouxiangongsi12"
							let sign=md5(signTemp)
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: time,
								nonceStr: sjstr,
								package: 'prepay_id='+res.data.data.prepay_id,
								signType: 'MD5',
								paySign: sign,
								success: (res) => {
									console.log(res)
									uni.showLoading({
										title: '正在查询中...'
									});
									uni.request({
										method:'GET',
										url: 'https://api.angeli.top/account.php?type=queryOrderIdAndVip', //仅为示例，并非真实接口地址。
										data:{
											auid:server.userinfo.Auid,
											orderId:dd,
											token:server.token
										},
										header: {
											'content-type': 'application/x-www-form-urlencoded',
											
										},
										success: (res) => {
											
											console.log(res)
											if(res.data.code==1){
												if(res.data.data.payStatus=='已支付' || res.data.data.payStatus=='OK'){
													console.log('已支付',this.endVipTime)
													uni.showToast({
														title: "已充值",
														position:'bottom',
														icon:'none',
														position:'center'
													})
													this.getjifen();
													this.$getUserinfo();
													this.$forceUpdate();
												}else{
													uni.showToast({
														title: "支付失败！请联系客服",
														position:'bottom',
														icon:'none',
														position:'center'
													})
												}
											}else{
												uni.showToast({
													title: "支付失败！请联系客服",
													position:'bottom',
													icon:'none',
													position:'center'
												})
											}
										},
										complete: () => {
											uni.hideLoading();
										}
									});
								},
								fail: function (err) {
									console.log(err)
									uni.showToast({
										title: "唤起支付失败！请联系客服",
										position:'bottom',
										icon:'none',
										position:'center'
									})
								}
							});
							
							
						}else{
				
						}
						
					},
					complete() {
						uni.hideLoading();
					}
				});

			}else{
				uni.showToast({
					title: "必须选择充值数量",
					position:'bottom',
					icon:'none',
					position:'center'
				})
			}
			
		},
		showKaitong:function(){
			this.showVip=true
			this.gaodu='0px'
			this.yanse='rgba(0,0,0,0.4)'
		},
		setxz:function(e){
			this.xz=e
			if(e==2){
				this.getBill();
			}
		},
		getBill:function(){
			uni.showLoading({
				title: '正在查询中...'
			}); 
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/account.php?type=cx', //仅为示例，并非真实接口地址。
				data:{
					auid:server.userinfo.Auid,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.billList=res.data.data
					}else{
						uni.showToast({
							title: "获取账单失败！",
							position:'bottom',
							icon:'none'
						});
					}	
				},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		inputVip:function(e){
			this.monnumber=e.target.value*10
			this.angelibi=e.target.value*10+"安个利币"
			if(e.target.value==""||e.target.value==null){
				this.angelibi="自定义"
			}
			if(e.target.value<1){
				uni.showToast({
					title: "最低充值不低于1元",
					position:'bottom',
					icon:'none'
				});
			}
		},
		hideVip:function(){
			this.showVip=false
			this.gaodu='-710px';
			this.yanse='rgba(0,0,0,0)'
		},
		xuanze:function(e){
			switch (e){
				case 1:
					this.monnumber=100
					break;
				case 2:
					this.monnumber=500
					break;
				case 3:
					this.monnumber=1000
					break;
				case 4:
					this.monnumber=100
					break;
				default:
					this.monnumber=100
					break;
			}
			this.xzId=e
			/* 新思路,点赞改变列表状态可以使用该方法,:class="当前ID是否等于列子ID，如果是那么就点赞，如果不是就没有点赞，"
			但是,这样只能存在一个点赞状态,其他的状态就会消失,带思考 */
		}
	}
}
